


















































































@import "@/assets/scss/variables.scss";
article {
  background-color: white;
  width: 100%;
  cursor: pointer;
  user-select: none;
  margin-bottom: 12px;
  padding-bottom: 18px;
  box-shadow: 0 1px 2px rgba($grey-cold-5,0.4);
  transition: box-shadow 0.2s ease-in-out;
  border-radius: 1px;

  &:hover {
    box-shadow: 0 1px 18px rgba($grey-cold-5,0.4);
  }

  &:not(.photo).flash {
    border-left: $urgency-bar-width solid #c9402d;
  }

  &.alerte {
    border-left: $urgency-bar-width solid $yellow-butter-5;
  }
  &.urgent {
    border-left: $urgency-bar-width solid lighten($yellow-butter-5, 20%);
  }
  &.viewed {
    background-color: mix($white, $background-color, 70);

    h2, p {
      color: $grey-cold-3;
    }
  }

  .cols {
    display: flex;
    justify-content: space-between;
    color: $grey-cold-4;
    padding: 18px 18px 0px 18px;
    font-size: 0.75rem;
    p.published {
      margin: 0px;
    }
    p.topshot {
      text-transform: uppercase;
      font-weight: 600;
      margin: 0px;
    }
  }

  .img-container + p {
    padding-top: 18px;
    margin-bottom: 0;
  }

  h2 {
    padding: 0 18px;
    font-size: 1.4rem;
    margin: 10px 0px;
    line-height: 1.4rem;
    letter-spacing: -0.045em;
    font-weight: 600;
    word-wrap: break-word;
  }

  .img-container {
    position: relative;
    width: 100%;
    height: 150px;
    background-size: 100% auto;
    background-position: center center;
    background-repeat: no-repeat;
    //margin: 10px 0px;
    border-radius: 2px;

    .UI-icon.UI-play-video {
      position: absolute;
      left: 0px;
      top: 50%;
      transform: translate(0%, -50%);
      font-size: 48px;
      color: white;
      opacity: 0.7;
      margin-left: 16px;
    }
  }

  p.lead {
    padding: 0px 18px 12px;
    display: block;
    display: -webkit-box;
    margin-top: 0px;
    margin-bottom: 0px;
    // max-height: 62px;
    word-wrap: break-word;
    overflow: hidden;
  }

}
