.column {
  min-width: $column-size;
  width: $column-size;
  height: 100%;
  margin-right: 5px;
  display: flex;
  flex-direction: column;
  scroll-snap-align: start;

  button {
    &.margin-left-auto {
      margin-left: auto;
    }
    &.margin-right-auto {
      margin-right: auto;
    }
  }

  header {
    position: relative;
    padding: 0px 17px 0px 12px;
    margin-top: 4px;

    .actions {
      display: flex;
    }
  }

  .form {
    /deep/ .vdp-datepicker {
      @import "~vue-content-placeholders/dist/vue-content-placeholders.css";
      width: 100%;
      margin-bottom: 24px;
      input {
        width: 100%;
        height: 100%;
        border: none;
        height: 48px;
        border-radius: 4px;
        padding: 5px 12px;
      }
      .vdp-datepicker__clear-button {
        position: absolute;
        font-size: 16px;
        top: 14px;
        right: 10px;
      }
    }
  }

  h2.error {
    text-align: center;
    padding: 5px;
  }

  .documents {
    flex: 1;
    padding: 4px 12px 12px 12px;

    .tombstone {
      padding: 12px;
      user-select: none;
    }

    .documents-gap {
      padding: 12px;
      text-align: center;
    }
  }
}

.curtain-enter {
  opacity: 0;
  transform: translateY(-30px);
}
.curtain-enter-active {
  transition: opacity 0.5s, transform 0.5s;
}
.curtain-enter-to {
  opacity: 1;
  transform: translateY(0px);
}

@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 28px 0;
  }
}
